<template>
  <div class="goodslist">
    <div class="search">
      <van-search
        shape="round"
        background="#e93323"
        placeholder="搜索商品信息"
      />
    </div>
    <div class="nav">
      <div class="active" @click.stop="go">{{ $route.query.title }}</div>
      <div :class="index > 0 ? 'active' : 'text'" @click.stop="price">
        价格
        <img :src="pic[index]" alt="" />
      </div>
      <div :class="ind > 0 ? 'active' : 'text'" @click.stop="salesvol">
        销量
        <img :src="pic[ind]" alt="" />
      </div>
      <div :class="cliflag ? 'active' : 'text'" @click.stop="news">新品</div>
    </div>
    <shop-list v-if="data.length>0"  :bastList="data"></shop-list>
     <div class="nolist" v-if="data.length<=0">
      <div class="nolistpic">
        <van-image
        :src="Nogood"
      />
      </div>
       <van-divider
            :style="{
              color: '#000',
              borderColor: '#282828',
              padding: '0 16px',
            }"
          >
            为你推荐
          </van-divider>
        <div>
          <shop-list :bastList="hotlist"></shop-list>
        </div>
    </div>
  </div>
</template>
<script>
import ShopList from "@/components/ShopList.vue";
import Nogood from "@/assets/noGood.png"
export default {
  data() {
    return {
      pic: [
        "data:image/png;base64,",
        "data:image/png;base64,",
        "data:image/png;base64,",
      ],
      index: 0,
      ind: 0,
      cliflag: false,
      data: [],
      priceOrder: "", //价格升降序
      salesOrder: "",//销量升降序
      new:0,//新品
      hotlist:[],
      Nogood
    };
  },
  components: {
    ShopList,
  },
  methods: {
    hot(){//热门商品
       this.$axios.get('/api/product/hot',{
         headers: { "Authori-zation": "Bearer " + this.tokens },
         params:{
           page: 1,
           limit: 20
         }
       }).then(d=>{
         console.log(d.data.data);
         this.hotlist=d.data.data;
       })
    },
    go() {
      this.$router.go(-1);
    },
    price() {//价格升降序
      this.index++;
      this.ind = 0;
      if (this.index == 1) {
        this.priceOrder = "asc";
        this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              priceOrder:this.priceOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      } else if (this.index == 2) {
        this.priceOrder = "desc";
         this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              priceOrder:this.priceOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      } else if (this.index > 2) {
        this.index = 0;
        this.priceOrder = "";
         this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              priceOrder:this.priceOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      }
    },
    salesvol() {//销量升降序
      this.ind++;
      this.index = 0;
      if(this.ind==1){
        this.salesOrder='asc';
        this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              salesOrder:this.salesOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      }else if(this.ind==2){
         this.salesOrder='desc';
          this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              salesOrder:this.salesOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      }else if (this.ind > 2) {
        this.ind = 0;
         this.salesOrder='';
         this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              salesOrder:this.salesOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      }
    },
    news() {//新品
      if (this.cliflag) {
        this.cliflag = false;
        this.new=0;
        this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              news:this.new,
              priceOrder:this.priceOrder,
              salesOrder:this.salesOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
        
      } else {
        this.cliflag = true;
        this.new=1;
        this.$axios.get("/api/products", {
            params: {
              sid: this.$route.query.id,
              title: this.$route.query.title,
              news:this.new,
              priceOrder:this.priceOrder,
              salesOrder:this.salesOrder
            },
          })
          .then((d) => {
            console.log(d);
            this.data = d.data.data.map((data) => {
              data = {
                ot_price: data.price,
                store_name: data.store_name,
                sales: data.sales,
                price: data.vip_price,
                image: data.image,
                id: data.id,
              };
              return data;
            });
            console.log(this.data);
          });
      }
    },
  },
  created() {
    // console.log(this.$route.query);
    this.$axios
      .get("/api/products", {
        params: {
          sid: this.$route.query.id,
          title: this.$route.query.title,
        },
      })
      .then((d) => {
        console.log(d);
        if(d.data.data.length>0){
            this.data = d.data.data.map((data) => {
            data = {
              ot_price: data.price,
              store_name: data.store_name,
              sales: data.sales,
              price: data.vip_price,
              image: data.image,
              id: data.id,
            };
            return data;
          });
        }else{
          this.hot()
        }
        
        console.log(this.data);
      });
  },
};
</script>
<style lang="less">
.goodslist {
  .nav {
    width: 100%;
    height: 43px;
    display: flex;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
    .text {
      flex: 1;
      text-align: center;
      line-height: 43px;
      font-size: 14px;
      img {
        width: 7.5px;
        height: auto;
      }
    }
    .active {
      color: #e93323;
      flex: 1;
      text-align: center;
      line-height: 43px;
      font-size: 14px;
      img {
        width: 7.5px;
        height: auto;
      }
    }
    
  }
    .nolist{
    background-color: white;
    margin-bottom: 40px;
    .nolistpic{
      width: 207px;
      margin: 0 auto;
      margin-top: 85px;
     
    }
    .van-divider{
      margin: 0;
      margin-top: 30px;
      margin-bottom: 20px;
    }
  }
}
</style>